<template>
  <div class="modal">
    <div class="modal__content" :class="modalSize">
      <button class="modal__close" @click="eventClose"></button>
      <!-- 샘플 div 영역 확인용 -->
      <div style="padding:160px 0; text-align:center">컨텐츠</div>
    </div>
    <div class="modal__bg"></div>
  </div>
</template>
<script>
import Constant from '@/Constant'
import { mapState } from 'vuex'

export default {
  name:'ModalPopup',
  computed:{
    ...mapState(['modalSize'])
  },
  methods:{
    eventClose:function(){
      this.$store.dispatch(Constant.MODAL_CLOSE)
    },
  }
}
</script>