import { createStore } from 'vuex'
import Constant from '@/Constant' ;

export default createStore({
  state: {
    modalView:null,
    modalSize:null,
    id:null
  },

  mutations: {
    //모달창 닫기
    [Constant.MODAL_CLOSE]:(state)=>{
      state.modalView = null
    },
    //모달창 열기
    [Constant.MODAL_OPEN]:(state, payload)=>{
      state.modalView = payload.modalView
      state.id = payload.id
    }
  },
  
  actions: {
    // 모달 창 닫기
    [Constant.MODAL_CLOSE]:(store)=>{
      store.commit(Constant.MODAL_CLOSE)
    },
    // 모달 창 열기 
    [Constant.MODAL_OPEN]:(store, payload)=>{
      store.commit(Constant.MODAL_OPEN, payload)
    }
  },
  modules: {
  }
})
