<template>
  <div class="mainview" style="height:40rem">
    <transition-group class="mainview__list" tag="ul" name="fade">
      <li v-for="n in [currentNumber]" :key="n">
          <img class="mainview__img" width="1920" height="400" :src="currentImage">
      </li>
    </transition-group>
    <div class="mainview__btngroup">
      <div>
        <span class="mainview__num"><b>{{currentPage + 1}}</b> / {{images.length}}</span>
        <button @click="prevBtn"><span class="icon-arrow-left"></span></button>
        <button @click="nextBtn"><span class="icon-arrow-right"></span></button>
      </div>
      <div>
        <button v-if="change == true" @click="stopRotation"><span class="icon-pause"></span></button>
        <button v-show="change == false" @click="startRotation"><span class="icon-play"></span></button>
      </div>
    </div>
</div>
</template>
<style lang="scss">
// 사용안함
  .mainview{position: relative;}
  .mainview__num{margin:0 2rem 0 1rem;}
  .mainview__btngroup {position: absolute; bottom: 1rem; right: 50%; margin-right: -45rem; display: flex;
    > div{display: flex; justify-content: center; align-items: center; margin: 0 .5rem; padding:0 1rem; min-width: 4rem; height: 4rem; border-radius: 3rem; font-size: 1.6rem; background:rgba(0,0,0,.3); color:#fff;
      &:hover { background: rgba(0,0,0,.5);}
      > button{ height: 100%; font-size: 1.4rem; color: #fff;
        & + button{margin:0 1rem 0 1.5rem;}
      }
    }
  }
</style>
<script>

export default {
  name: "MainView" ,
  data: ()=>({
    images: [require('@/assets/img/type2.png'),require('@/assets/img/type3.png'),require('@/assets/img/type4.jpg')],
    currentNumber: 0,
    timer: null,
    change : true
  }),
  mounted(){
    this.startRotation()
  },
  computed: {
    currentImage() {
      return this.images[Math.abs(this.currentNumber) % this.images.length];
    },
    currentPage() {
      let Num = this.currentNumber
      let Full = this.images.length
      let cp = Num.toString(Full)
      return parseInt(cp.charAt(cp.length-1))
    }
  },
  methods: {
    prevBtn(){
      this.currentNumber -= 1
    },
    nextBtn(){
      this.currentNumber += 1
    },
    startRotation(){
      this.timer = setInterval(this.nextBtn, 3000);
      this.change = true
    },
    stopRotation() {
      clearTimeout(this.timer);
      this.timer = null;
      this.change = false
    },


  }
}
</script>