<template>
    <div>
        <ul class="productinfo__optlist">
            <li v-for=" (i,index) in optionlist" :key="index.reverse">
                <p class="productinfo__txt">{{i.name}}</p>
                <div class="flex">
                    <div class="quantity">
                        <button type="button" class="quantity__btn" @click="i.count -= 1"><span class="icon-minus"></span></button>
                        <input type="text" :value="i.count" class="quantity__tf">
                        <button type="button" class="quantity__btn" @click="i.count += 1"><span class="icon-plus"></span></button>
                    </div>
                    <p class="productinfo__txt text_bold ml_auto"><span class="price">{{ `${i.value*i.count}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</span>
                        <button class="btn_icon" type="button" @click="doRemove(index)"><span class="icon-close"></span></button>
                    </p>
                </div>
            </li>
        </ul>
        <p class="productinfo__txt"><b>총 상품금액</b><b class="price" style="font-size:3.2rem">{{ `${totalprice}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</b></p>
    </div>
</template>

<script>
export default {
    data: function() {
        return {
            count2: 0
        }
    },
    props: {
        optionlist : Array,
    },
    computed:{
        totalprice : function() {
            // 합계
            const v = this.optionlist.map(item => item.value * item.count ).reduce((prev, curr) => prev + curr, 0)
            return v
       },
       count: function() {
           const c = this.optionlist.map(item => item.value * item.count )
           return c
       }
    },
    methods: {
        minus: function() {
            console.log(this.count2);
            if(this.count2 > 1) return this.count2 -= 1 
        },
        doRemove: function(e) {
            console.log(e)
            this.$props.optionlist.splice(e,1)
        }
    }
}
</script>