<template>
    <div class="modal">
        <form class="flex">
            <div class="modal__box">
                <p class="modal__tit">배송지 등록/수정
                    <button @click="eventClose" class="btn_icon ml_auto">
                        <span class="icon-close"></span>
                    </button>
                </p>
                <div class="modal__cont">
                    <table class="tbl_comm">
                        <col style="width:20rem">
                        <col style="width:auto">
                        <tr>
                            <th class="text_bold">받는분</th>
                            <td colspan=""><input type="text" class="tf_comm" value="고길동"></td>
                        </tr>
                        <tr>
                            <th class="text_bold">연락처</th>
                            <td colspan=""><input type="text" class="tf_comm" value="000-0000-0000"></td>
                        </tr>
                        <tr>
                            <th class="text_bold">받는 주소</th>
                            <td>
                                <input type="text" class="tf_comm">
                                <button class="btn_comm">우편번호 검색</button>
                                <br>
                                <input type="text" class="tf_comm" style="width:100%">
                                <input type="text" class="tf_comm" style="width:100%">
                            </td>
                        </tr>
                    </table>     
                </div>
                <div class="modal__btngroup">
                    <button class="btn_comm" @click="eventClose">취소</button>
                    <button class="btn_comm" @click="eventClose">확인</button>
                </div>
            </div>
        </form>

        <div class="modal__bg"></div>
    </div>
</template>
<script>
import Constant from '@/Constant'
import { mapState } from 'vuex'

export default {
    name:'ModalAddress',
    computed:{
        ...mapState(['modalSize'])
    },
    methods:{
        eventClose:function(){
            this.$store.dispatch(Constant.MODAL_CLOSE)
        },
        eventOpenModal:function(e){
            this.$store.dispatch(Constant.MODAL_OPEN,{modalView:'ModalCart',modalSize:e})
        }
    }
}
</script>