<template>
    <div class="modal">
        <form class="flex">
            <div class="modal__box" style="width:65rem; min-height:63rem">
                <p class="modal__tit">옵션선택
                    <button @click="eventClose" class="btn_icon ml_auto"><span class="icon-close"></span></button>
                </p>
                <div class="modal__cont">
                    <div class="productinfo productinfo--small">
                        <div class="flex">
                            <div class="productinfo__imgbox small">
                                <img :src="item.img1" v-if="baseimg == null">
                                <img :src="baseimg" v-else>
                            </div>
                            <div class="productinfo__infobox">
                                <p class="productinfo__txt">{{item.Sname}}</p>
                                <p class="productinfo__tit">{{item.name}}</p>
                                <hr class="productinfo__hr">
                                <p class="productinfo__txt">정가<del class="price">{{`${item.oprice}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</del></p>
                                <p class="productinfo__txt">판매가
                                    <b class="text_red" style="margin-left:18rem">{{(item.oprice - item.price) / item.oprice * 100}}% ↓</b>
                                    <b class="price">{{`${item.oprice}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</b>
                                </p>
                                <p class="productinfo__txt" style="margin-top:-.6rem; font-size:1.3rem"><span class="ml_auto">(103,000 원/개)</span></p>
                                <hr class="productinfo__hr">
                                <p class="productinfo__txt">배송 안내</p>
                                <table class="tbl_comm">
                                    <col style="width:11.5rem">
                                    <tr>
                                        <th class="text_left">택배<br/><span style="font-size:1.13rem">(묶음배송 불가)</span></th>
                                        <td class="text_gray">
                                            3000원 (20개 마다 부과)<br>
                                            ※ 도서산간 지역 배송비 추가 (별도입금)
                                        </td>
                                    </tr>
                                    <tr>
                                        <th class="text_left">직접배송<br><span style="font-size:1.13rem">(판매자별 묶음배송)</span></th>
                                        <td class="text_gray">
                                            별도<br>
                                            ※ 경기도, 충청도 외 직접배송 불가
                                        </td>
                                    </tr>
                                </table>
                                <!-- 상품이 여러 개일 경우 -->
                                <div v-if="item.goodstype">
                                    <select class="productinfo__opt" v-model="selected" @change="doAdd($event)">
                                        <option disabled value="">상품을 선택해주세요</option>
                                        <option v-for="(opt, index) in item.option" :key="index" :value="{name:opt.name, value:opt.value, id:opt.id}" >{{opt.name}}</option>
                                    </select>
                                    <inner-option :optionlist = this.optionlist></inner-option>
                                </div>
                                <!-- 상품이 한 개일 경우 -->
                                <div v-else>
                                    <hr class="productinfo__hr">
                                    <p class="productinfo__txt">수량 &nbsp;<span class="text_gray">※ 최소 주문수량 1개 이상</span></p>
                                    <div class="flex">
                                        <div class="quantity">
                                            <button type="button" class="quantity__btn" @click="minus()"><span class="icon-minus"></span></button>
                                            <input type="text" :value="count" class="quantity__tf">
                                            <button type="button" class="quantity__btn" @click="count += 1"><span class="icon-plus"></span></button>
                                        </div>
                                        <p class="productinfo__txt text_bold ml_auto"><span class="price">{{`${item.price*count}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</span></p>
                                    </div>
                                    <p class="productinfo__txt"><b>총 상품금액</b><b class="price" style="font-size:3.2rem">{{ `${item.price*count}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',') }}</b></p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="modal__btngroup">
                    <button class="btn_comm" type="button" @click="eventClose">취소</button>
                    <button class="btn_comm blue text_bold" type="button" @click="eventOpenModal">장바구니 담기</button>
                </div>
            </div>
        </form>
        <div class="modal__bg"></div>
    </div>
</template>
<script>
import Constant from '@/Constant'
import { mapState } from 'vuex'
import itemslist from '@/views/DataSet.json' 
import InnerOption from '@/components/InnerOption';

export default {
    name:'ModalOption',
    data : function() {
        return {
            items : itemslist.items,
            baseimg : null,
            count : 1,
            optionlist: [],
            selected: ''
        }
    },
    components :{
        InnerOption
    },
    computed : {
        item : function() {
            let id = this.id;
            let arr = this.items.filter(function(item) {
                return item.id == id;
            });

            if (arr.length == 1) return arr[0];
            else return {};
        },
        ...mapState(['modalSize','id'])
    }, 
    methods:{
        eventClose:function(){
            this.$store.dispatch(Constant.MODAL_CLOSE)
        },
        eventOpenModal:function(e){
            this.$store.dispatch(Constant.MODAL_OPEN,{modalView:'ModalCart',modalSize:e})
        },
        minus:function(){
            if(this.count > 1) return this.count -= 1 
        },
        doAdd: function () {
            console.log(this.selected)

            // 새로운 리스트에 추가하기
            this.optionlist.push({
                id: this.selected.id,
                name: this.selected.name,
                value: this.selected.value,
                count: 1
            })
        },
    }

}
</script>