<template>
    <div class="modal">
        <div class="modal__box small">
            <p class="modal__tit">장바구니 담기
                <button @click="eventClose" class="btn_icon ml_auto">
                    <span class="icon-close"></span>
                </button>
            </p>
            <div class="modal__cont">
                <span class="icon-shopping-basket text_center"></span>
                <p class="text_center" style="font-size:1.8rem">
                    
                    <b>상품이 장바구니에 담겼습니다.</b><br>
                    <span>바로 확인하시겠나요?</span>
                </p>
            </div>
            <div class="modal__btngroup">
                <a href="javascript:void(0);" @click="eventClose" class="btn_comm">쇼핑 계속하기</a>
                <router-link to="/CartList" class="btn_comm" @click="eventClose">장바구니 이동</router-link>
            </div>
        </div>

        <div class="modal__bg"></div>
    </div>
</template>
<script>
import Constant from '@/Constant'
import { mapState } from 'vuex'

export default {
    name:'ModalCart',
    computed:{
        ...mapState(['modalSize'])
    },
    methods:{
        eventClose:function(){
            this.$store.dispatch(Constant.MODAL_CLOSE)
        },
    }

}
</script>