<template>
    <div>
        <div class="sortoption" v-if="post.sortgroup">
            <div class="mr_auto">
                <label class="radio_comm">
                    <input type="radio" name="ra1" checked> 전체상품
                </label>
                <label class="radio_comm">
                    <input type="radio" name="ra1"> 택배상품
                </label>
                <label class="radio_comm">
                    <input type="radio" name="ra1"> 직배상품
                </label>
            </div>
            <button type="button" class="active">판매인기순</button>
            <button type="button">낮은 가격순</button>
            <button type="button">높은 인기순</button>
            <button type="button">가나다순</button>
            <button type="button">최신등록순</button>
        </div>
        <div class="listoption" v-if="post.listoption">
            <label class="checkbox_comm" v-if="!show"><input type="checkbox"> 전체선택</label>
            <div class="ml_auto">
                <select class="opt_comm bg_white" style="margin-right:.5rem">
                    <option>20개 보기</option>
                    <option>50개 보기</option>
                    <option>100개 보기</option>
                    <option>200개 보기</option>
                </select>
                <div class="btn_group">
                    <button class="btn_comm" :class="{active : show == true}" @click="listShow(true)" style="width:8rem">크게보기</button>
                    <button class="btn_comm" :class="{active : show == false}" @click="listShow(false)" style="width:8rem">리스트보기</button>
                </div>
            </div>
        </div>
        <div class="gridview" v-if="show">
            <ul class="gridview__list">
                <li v-for="(item) in items" :key="item.id">
                    <div class="product" :class="post.imgsize">
                        <router-link :to="'/GoodsDetail/'+item.id" class="product__imgbox">
                            <!-- 배너 -->
                            <div class="badge">
                                <div class="badge__event" v-if="item.event"><img src="@/assets/img/badge_specialprice.png"></div>
                                <div class="badge__best" v-if="item.best"><img src="@/assets/img/badge_best.png"></div>
                                <div class="badge__new" v-if="item.new"><img src="@/assets/img/badge_new.png"></div>
                            </div>
                            <!-- 품절표시 -->
                            <div class="product__soldout" v-if="item.state">품절</div>
                            <!-- hover 표시 -->
                            <!-- <div class="product__hoverbox">
                                <div class="product__btnbox">
                                    <router-link to="/FavoritesList" class="btn_comm"><span class="icon-heart-fill" :class="{active: item.favori}"></span>찜하기</router-link>
                                    <button @click="eventOpenModal(item.id)" class="btn_comm"><span class="icon-shopping-basket"></span>장바구니</button>
                                </div>
                            </div> -->
                            <img :src="item.img1" alt="상품이미지" >
                        </router-link>
                        <router-link :to="'/GoodsDetail/'+item.id" class="product__name" v-if="item.name.length < 20">
                            <span style="display:block">{{item.name}}</span>
                            <b class="text_blue" style="display:block">10입</b>
                        </router-link>
                        <router-link :to="'/GoodsDetail/'+item.id" class="product__name" v-else>
                            <span>{{item.name}}</span>
                            <b class="text_blue">10입</b>
                        </router-link>
                        <p class="product__price"><b>27%↓</b>
                            <span>{{`${item.price}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}}</span>
                        </p>
                        <p class="text_center">({{Math.ceil(`${item.price/item.box}`)}}원/개)</p>
                        <!-- 추가 -->
                        <div class="flex flex_column" style="margin-top:1rem">
                            <div class="quantity ma_auto">
                                <button class="quantity__btn"><span class="icon-minus"></span></button>
                                <input type="text" class="quantity__tf" value="1">
                                <button class="quantity__btn"><span class="icon-plus"></span></button>
                            </div>
                            <div class="product__btngroup">
                                <button class="btn_comm blue" :class="{'disabled': item.parcel === false}">택배담기</button>
                                <button class="btn_comm teal" :class="{'disabled': item.direct === false}">직배담기</button>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <div class="listview" v-if="!show">
            <ul class="listview__list">
                <li v-for="(item) in items" :key="item.id">
                    <div class="product2" :class="post.imgsize">
                        <label class="checkbox_comm">
                            <input type="checkbox" />
                        </label>
                        <div class="product2__imgbox">
                            <div class="product2__soldout" v-if="item.state">품절</div>
                            <img :src="item.img1" alt="상품이미지" >
                        </div>
                        <div class="product2__tit">
                            <p class="badge2">
                                <span v-if="item.best" class="badge2__icon red">특가</span>
                                <span v-if="item.new" class="badge2__icon yellow">NEW</span>
                                <span class="badge2__icon green">면</span>
                                <span class="badge2__icon blue">냉장</span>
                                <span class="badge2__icon indigo">냉동</span>
                            </p>
                            <router-link class="" :to="'/GoodsDetail/'+item.id"><b>{{item.name}}</b> <b class="text_blue">10입</b></router-link>
                        </div>

                        <div style="width:17rem;">
                            <p class="text_center"><b class="text_red">27%↓</b> <b v-text="item.price"></b> 원</p>
                            <p class="text_center">(<b>2,800</b> 원/개)</p>
                        </div>
                        <div style="width:13rem;">
                            <div class="quantity">
                                <button type="button" class="quantity__btn"><span class="icon-minus"></span></button>
                                <input type="text" class="quantity__tf">
                                <button type="button" class="quantity__btn"><span class="icon-plus"></span></button>
                            </div>
                        </div>
                        <div class="">
                            <div class="tooltip" :class="{'active': item.parcel === true}">
                                <button class="btn_comm blue" :class="{'disabled': item.parcel === false}">택배담기</button>
                                <span class="tooltip__info">
                                    <table>
                                        <tr>
                                            <th rowspan="5" class="gray">택배</th>
                                            <td>배송비</td>
                                            <td>-수량당 3,000원</td>
                                        </tr>
                                        <tr>
                                            <td>배송비 포함단가</td>
                                            <td>- 3,025원/개</td>
                                        </tr>
                                        <tr>
                                            <td>최소주문금액</td>
                                            <td>- 없음</td>
                                        </tr>
                                        <tr>
                                            <td>무료배송기준</td>
                                            <td>- 5000,000원 이상 주문</td>
                                        </tr>
                                        <tr>
                                            <td>예상소요일</td>
                                            <td>- 4일 이내</td>
                                        </tr>
                                    </table>
                                </span>
                            </div>
                            <div class="tooltip" :class="{'active': item.direct === true}">
                                <button class="btn_comm teal" :class="{'disabled': item.direct === false}">직배담기</button>
                                <span class="tooltip__info">
                                    <table>
                                        <tr>
                                            <th rowspan="5" class="gray">직배</th>
                                            <td>배송비</td>
                                            <td>-수량당 3,000원</td>
                                        </tr>
                                        <tr>
                                            <td>최소주문금액</td>
                                            <td>- 없음</td>
                                        </tr>
                                        <tr>
                                            <td>무료배송기준</td>
                                            <td>- 5000,000원 이상 주문</td>
                                        </tr>
                                        <tr>
                                            <td>예상소요일</td>
                                            <td>- 4일 이내</td>
                                        </tr>
                                    </table>
                                </span>
                            </div>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <pagination v-if="post.pagination"></pagination>
        <fix-option v-if="!show" :post="this.post"></fix-option>
    </div>
</template>
<script>
import Constant from '@/Constant'
import Pagination from './Pagination.vue'
import FixOption from '@/components/FixOption.vue'

export default {
  components: { 
        Pagination,
        FixOption,
    },
    name: 'GridView',
    data: function(){
        return {
            show: true,
            color: 'cyan',
            color2: 'blue',
        }
    },
    props: {
        items : Array,
        catelist: Array,
        post: {String}
    },
    computed: {
        itemprice(){
            return this.items.map(item => `${item}`.replace(/\B(?=(\d{3})+(?!\d))/g, ','))
        },

    },
    methods:{
        changeColor:function(){
            if (this.color === 'cyan') {
                this.color = 'green' 
            } else if (this.color === 'green') {
                this.color = 'yellow'
            } else if (this.color === 'yellow') {
                this.color = 'gray'
            } else if (this.color === 'gray') {
                this.color = 'pink'
            } else if (this.color === 'pink') {
                this.color = 'violet'
            } else if (this.color === 'violet') {
                this.color = 'blue'
            } else if (this.color === 'blue') {
                this.color = 'teal'
            } else if (this.color === 'teal') {
                this.color = 'lime'
            } else if (this.color === 'lime') {
                this.color = 'orange'
            } else if (this.color === 'orange') {
                this.color = 'cyan'
            }
        },
        changeColor2:function(){
            if (this.color2 === 'cyan') {
                this.color2 = 'green' 
            } else if (this.color2 === 'green') {
                this.color2 = 'yellow'
            } else if (this.color2 === 'yellow') {
                this.color2 = 'gray'
            } else if (this.color2 === 'gray') {
                this.color2 = 'pink'
            } else if (this.color2 === 'pink') {
                this.color2 = 'violet'
            } else if (this.color2 === 'violet') {
                this.color2 = 'blue'
            } else if (this.color2 === 'blue') {
                this.color2 = 'teal'
            } else if (this.color2 === 'teal') {
                this.color2 = 'lime'
            } else if (this.color2 === 'lime') {
                this.color2 = 'orange'
            } else if (this.color2 === 'orange') {
                this.color2 = 'cyan'
            }
        },



        eventOpenModal:function(e){
            this.$store.dispatch(Constant.MODAL_OPEN,{modalView:'ModalOption',id:e})
        },
        listShow:function(e){
            if(e == !true){
                this.show = false
            } else if (e == true){
                this.show = true
            }
        },
    }
}
</script>
