import { createRouter, createWebHistory } from 'vue-router'
import MainPage from '@/views/MainPage.vue'
import CategoryList from '@/views/CategoryList.vue'
import BestProductsList from '@/views/BestProductsList.vue'
import NewProductsList from '@/views/NewProductsList.vue'
import CartList from '@/views/CartList.vue'
import StoreList from '@/views/StoreList.vue'

const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage
  },
  { path: '/CategoryList', name: 'CategoryList', component: CategoryList },
  { path: '/BestProductsList', name: 'BestProductsList', component: BestProductsList },
  { path: '/NewProductsList', name: 'NewProductsList', component: NewProductsList },
  { path: '/CartList', name: 'CartList', component: CartList },
  { path: '/StoreList', name: 'StoreList', component: StoreList },
  { path: '/StoreProductsList', name: 'StoreProductsList',component: () => import('@/views/StoreProductsList')},
  { path: '/MyPage', name:'MyPage',component: () => import('@/views/MyPage') },
  { path: '/OrderList', name:'OrderList', component: () => import('@/views/OrderList.vue') },
  { path: '/OrderDetail',name:'OrderDetail', component: () => import('@/views/OrderDetail.vue') },
  { path: '/OrderForm', component: () => import('@/views/OrderForm.vue') },
  { path: '/OrderResult', component: () => import('@/views/OrderResult.vue') },
  { path: '/FavoritesList', component: () => import('@/views/FavoritesList.vue') },
  { path: '/AddressList', component: () => import('@/views/AddressList.vue') },
  { path: '/NoticeList', component: () => import('@/views/NoticeList.vue') },
  { path: '/NoticeDetail', component: () => import('@/views/NoticeDetail.vue') },
  { path: '/GoodsList/:id', component: () => import('@/views/GoodsList.vue') },
  { path: '/GoodsDetail/:id', component: () => import('@/views/GoodsDetail.vue') },


  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
